//prd kong的生产环境
let path = {
    account: api + '/get',
    host: api + '/api/v1/finac',
    //code 换token
    code: api + "/personalCenter/login",
    risk: wxhost + '/personal/dist/html/p-risk-evaluation.html',
    getUrl(urlId){
        var b = 'u' + urlId;
        return path.host + path.suffix[b];
    },
    suffix: {
        u1000: '/product/fund/quote', //货币基金历史行情
        u1001: '/product/fund/list',// 货币基金列表
        /*by fanling amend*/
        u1002: '/account/apply', //绑卡申请
        u1003: '/account/confirm', // 绑卡确认
        u1004: '/account/delete', // 删除银行卡
        u1005: '/account/payment', //支付账户信息查询
        u1006: '/trade/purchase', //快捷充值
        u1008: '/trade/redeem', //资金提现
        u1010: '/password/modify', //理财密码修改
        u1011: '/password/forgot/apply', //理财密码找回申请
        u1012: '/password/forgot/confirm', //理财密码找回验证
        u1013: '/bank/list', //银行列表查询
        u1014: '/trade/position', //持仓查询
        u1015: '/trade/flow', //交易明细查询
        u1016: '/trade/income', //收益明细查询
        u1017: '/account/exists', //客户查询
        uRiskLeve: '/account/risk', //risk level
        uMatch: '/account/risk/match',
        u1018:'/account/change/apply',//更换基金申请
        u1019:'/account/change/confirm',//更换基金确认
    }
};
export default path


