<template>
	<div class="alert-container">
		<div class="alert-nav">
			<p>{{alertTop }}</p>
			<p  class="alert-bottomline">{{ alertText}}</p>
			<span @click="$emit('left')">{{alertBarLeft}}</span>
			<span @click="$emit('right')">{{alertBarRight}}</span>
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			alertTop: {
				type: String,
				required: false,
				default: function() {
					return '温馨提示';
				}
			},
			alertText: {
				type: String,
				required: false,
				default: function() {
					return '';
				}
			},
			alertBarLeft: {
				type: String,
				required: false,
				default: function() {
					return '取消';
				}
			},
			alertBarRight: {
				type: String,
				required: false,
				default: function() {
					return '重新获取';
				}
			}
		}
	};
</script>

<style lang="scss">
	.alert-container {
		z-index: 999;
		position: fixed;
		z-index: 2;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		width: 100%;
		font-size: .3rem;
		background-color: rgba(0, 0, 0, 0.3);
		.alert-nav {
			background: #fff;
			border-radius: 8px;
			position: absolute;
			left: 50%;
			top: 50%;
			width: 80%;
			margin-left: -40%;
			margin-top: -2rem;
			span {
				display: inline-block;
				width: 48%;
				text-align: center;
				border-right: 1px solid #ccc;
				color: #f22f33;
				height: 0.88rem;
				line-height: 0.88rem;
			}
			span:last-child {
				border-right: none;
			}
			p {
				font-size: .32rem;
				text-align: center;
			}
			p:nth-child(1) {
				font-size: 0.34rem;
				font-weight: bold;
				color: #484848;
				margin-top: 0.55rem;
			}
			p:nth-child(2) {
				padding: 0 0.2rem .7rem 0.2rem;
				
				margin-top: 0.4rem;
			}
			.alert-gone {
				text-align: center;
				color: #f22f33;
				height: 0.88rem;
				line-height: 0.88rem;
			}
		}
	}
	.alert-bottomline{
		position: relative;
	}
	.alert-bottomline:after {
		    position: absolute;
		    content: '';
		    width: 100%;
		    left: 0;
		    bottom: 0;
		    height: 1px;
		    background-color: #d2d2d2;
		    -webkit-transform: scale(1,.5);
		    transform: scale(1,.5);
		    -webkit-transform-origin: center bottom;
		    transform-origin: center bottom
		}
</style>


