<template>
    <div id="app">
        <!--<transition :name="transitionName" keep-alive>-->
            <router-view class="view"></router-view>
        <!--</transition>-->
        <loading v-show="isLoading"></loading>
        <alerttwo :alertText="initFailure.text" @left="cancelAlert" @right="retry" v-show="isInitFailed"></alerttwo>
        <!--<div class="t_back" @click="t_back()"><span><</span></div>-->
        <div class="t_back" @click="t_back()" v-if="t_IndexBack">
            <img src="./assets/img/btnBack.png" alt="">
        </div>
        <!--<div class="t_back" @click="t_home()" v-if="t_IndexHome">-->
            <!--<img src="./assets/img/btnHome.png" alt="">-->
        <!--</div>-->
    </div>
</template>
<script>
//    $route.prototype.t_back = function () {
//        this.isBack = true
//        window.history.go(-1)
//    }
    import Loading from './components/Loading.vue'
    import alerttwo from './components/alerttwo2.vue'
    export default {
        data() {
            return {
                transitionName: 'slide-left',
                isBack:false
            }
        },
        components: {
            Loading,
            alerttwo
        },
        watch: {
            '$route' (to, from) {
                let isBack = this.isBack;
                //  监听路由变化时的状态为前进还是后退
                if(isBack) {
                    this.transitionName = 'slide-right'
                } else {
                    this.transitionName = 'slide-left'
                }
                this.isBack = false
            }
        },
        computed: {
            isLoading() {
                return this.$store.getters.isLoading
            },
            isInitFailed() {
                return this.$store.getters.isInitFailed
            },
            initFailure() {
                return this.$store.getters.initFailure
            },
            t_IndexBack() {
                let path = this.$route.path
                console.log(path);
                return path != '/' && path != '/myWallet'
                    && path != '/transferResult'
                    && path != '/transferOutResult'
                    && path != '/openAccountsSuccess'
//                    && path != '/openAccounts1st'

            },
            t_IndexHome() {
                let path = this.$route.path
                console.log(path)
                return path == '/myWallet'
            },

        },
        methods: {
            cancelAlert() {
                this.$store.commit('setInitFailed', false)
            },
            retry() {
                this.$store.commit('setInitFailed', false)
                console.log(this.initFailure.action)
                this.$store.dispatch(this.initFailure.action)
            },
            t_back() {
//              this.transitionName = 'slide-right'
                this.$router.back(-1);
                this.isBack = true

            },
            t_home() {
                this.$router.push("/")
                this.isBack = true
                this.transitionName = 'slide-right'
            }
        }
    }
</script>

