<template>
	<!-- <div id="loadingcomp">
    <div id="loadingmask"></div>
    <div id="loading">
      <div id="loading-animation"></div>
      <p>正在加载...</p>
    </div>
  </div> -->
	<div class="loading-wrap">
		<div class="loading-mask"></div>
		<div class="loading-box-opa"></div>
		<div class="loading-box">
			<div class="loading-ring"></div>
			<div class="loading-logo"></div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'loading',
		data() {
			return {}
		}
	}
</script>

<style>
	.loading-wrap {
		height: 100%;
		width: 100%;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 998;
	}
	
	.loading-mask {
		height: 100%;
		background: #fff;
		opacity: 0
	}
	
	.loading-box-opa {
		height: 1.77rem;
		width: 1.77rem;
		border-radius: .1rem;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		background: #000;
		opacity: .6;
	}
	
	.loading-box {
		height: 1.77rem;
		width: 1.77rem;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
	}
	
	.loading-logo {
		height: .64rem;
		width: .64rem;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		/*background: url("data:image/png;base64,") no-repeat 0 0;*/
		background: url("data:image/png;base64,");
		background-size: contain;
	}
	
	.loading-ring {
		position: absolute;
		left: 50%;
		top: 50%;
		height: 1.15rem;
		width: 1.15rem;
		margin-left: -0.575rem;
		margin-top: -0.575rem;
		/*原图*/
		/*background: url("data:image/png;base64,") no-repeat 0 0;*/
		/*重新切图更改为*/
		background: url("data:image/png;base64,") no-repeat 0 0;
		background-size: contain;
		-webkit-transform: translate3d(0, 0, 0);
		-webkit-animation: loading-keyframe 1s infinite linear;
		animation: loading-keyframe 1s infinite linear;
	}
	
	@-webkit-keyframes loading-keyframe {
		0% {
			-webkit-transform: rotate(0deg)
		}
		to {
			-webkit-transform: rotate(-360deg)
		}
	}
</style>


