import Vue from 'vue'
import axios from 'axios'
import _path from '../assets/js/path.js'
import qs from 'qs';
import store from '../store/index.js'
const ax = {
    //用户信息
    getUserInfo(){
        let _data = {
            token: store.getters.token,
            sysNo: 'CSE006',
        }
        console.log('getUserInfo请求了')
        return axios({
            method: 'post',
            //url: 'http://10.23.179.67:8000/get',
            url: _path.account,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: qs.stringify(_data)
        })
    },
    //风险等级 内置参数
    getRiskData() {
        console.log('getRiskData请求了')
        let riskData = {
            sysNo: 'CSE006',
            paperType: 1,
            paperVersion: 2,  // 20170629  风险测评加V2参数
            token: store.getters.token
        }
        return axios({
            method: 'post',
            url: _path.getUrl('RiskLeve'),
            data: qs.stringify(riskData)
        })
    },
    //1000 货币基金历史行情 pdCode
    getFundQuote(data) {
        console.log('getFundQuote请求了');
        console.log(this.$store.state.fundType)
        console.log(this.$store.state.newFundType)
        return axios({
            method: 'post',
            url: _path.getUrl(1000),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: qs.stringify({pdCode: 1001})
            // data: qs.stringify({
            //   pdCode: 1001, //点击获取
            // })
        })
    },
    //1001 货币基金列表 无参数
    getFundList(){
        // console.log('store.getters.hasGetFundList')
        // console.log(store.getters.hasGetFundList)
        // if(store.getters.hasGetFundList){
        console.log('getFundList请求了');
        return axios({
            method: 'post',
            url: _path.getUrl(1001),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            }
        })
        // }
    },
    //1005支付账户信息查询 内置token
    getPaymentData(bizType) {
        let _data = {token: store.getters.token}
        if (!!bizType) {
            _data['bizType'] = bizType
        }
        console.log(_data)
        console.log('getPaymentData请求了');
        return axios({
            method: 'post',
            url: _path.getUrl(1005),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: qs.stringify(_data)
            // data: qs.stringify({
            //   token: context.getters.token
            // })
        })
    },
    //1013  银行列表查询  //无输入
    getBankList() {
        let _data = {payChannel: store.state.exists.payChannel}
        console.log('getBankList请求了');
        return axios({
            method: 'post',
            url: _path.getUrl(1013),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            // url: 'http://27.223.31.46:7771/api/v1/finac/bank/list',
            //data:context.state.test02
            data: qs.stringify(_data)
        })
    },
    //1014  持仓查询  /trade/position.json
    getPositionData() {
        let _data = {token: store.getters.token}
        console.log('getPositionData请求了');
        return axios({
            method: 'post',
            url: _path.getUrl(1014),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: qs.stringify(_data)
            // data: qs.stringify({
            //   token: context.getters.token
            // })
        })
    },
    //1015  交易明细查询  /trade/flow.json
    getFlowData(data) {
        console.log('getFlowData请求了');
        return axios({
            method: 'post',
            url: _path.getUrl(1015),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: qs.stringify(data)
        })
    },
    //1016 收益明细查询
    getIncomeData(data) {
        console.log('getIncomeData请求了');
        return axios({
            method: 'post',
            url: _path.getUrl(1016),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            //data : data
            data: qs.stringify(data)
        })
    },
    //exists 内置token

    getAccountExists() {
        //if(!store.getters.hasGetUserInfo){
        let _data = {token: store.getters.token}
        console.log('getAccountExists请求了')
        return axios({
            method: 'post',
            url: _path.getUrl(1017),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: qs.stringify(_data)
            // data: qs.stringify({
            //   token: context.getters.token
            // })
        })
        //}
    },
    //1002
    bindCardApply(data) {
        data.token = store.getters.token
        return axios({
            method: 'post',
            url: _path.getUrl(1002),
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: qs.stringify(data)
        })
    },
    //code换token
    getCode(data){
        return axios({
            method: 'get',
            url: _path.code,
            withCredentials: true,
            headers: {
                // 'Access-Control-Allow-Headers':'Content-Type, Authorization',
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            params: {
                code: data
            },
        })
    },
}

export default ax


