const serviceAgreementView = resolve => require(['../pages/serviceAgreementView.vue'], resolve)
const transferOutView = resolve => require(['../pages/transferOutView.vue'], resolve)
const transferOutResultView = resolve => require(['../pages/transferOutResultView.vue'], resolve)
